import {
    Component,
    h,
    Prop,
    State,
    Element,
    EventEmitter,
    Event,
    Host,
} from '@stencil/core';
import { DateFormat, MIN_DATE, MAX_DATE } from '../../types';
import { fetchTranslations } from '../../utils/translation';
import { TimeDatePresentationUtil } from '../../utils/time-date-presentation.util';
import { classlist } from '../../utils/classlist';

const inputVariantsConfig = {
    variants: {
        disabled: {
            false: 'focus-within:ring-active-foreground/30 bg-white',
            true: 'bg-surface-1 !text-disabled pointer-events-none border-none',
        },
        readonly: {
            false: 'focus-within:ring-active-foreground/30',
            true: '!bg-surface-1 !text-dark cursor-text border-none',
        },
        invalid: {
            false: 'border-med focus-within:border-active-dark focus-within:ring-active-foreground/30',
            true: 'border-error-base focus-within:border-error-base focus-within:ring-destructive-foreground/30',
        },
    },
    defaultVariants: {
        disabled: false,
        readonly: false,
        invalid: false,
    },
};

@Component({
    tag: 'at-input-date',
    shadow: false,
})
export class AtInputDate {
    /**
     * Minimum selectable date
     */
    @Prop({ mutable: true }) min_date: Date = MIN_DATE;

    /**
     * Maximum selectable date
     */
    @Prop({ mutable: true }) max_date: Date = MAX_DATE;

    /**
     * Label text displayed above the date picker
     */
    @Prop() label?: string;

    /**
     * Short description or validation hint if required
     */
    @Prop() hint_text: string;

    /**
     * Optional info icon with detailed tooltip description
     */
    @Prop() info_text?: string;

    /**
     * Error text displayed when invalid is set
     */
    @Prop() error_text: string;

    /**
     * Current value of the selected date
     */
    @Prop({ mutable: true }) value: Date;

    /**
     * Adds a required * to the element
     */
    @Prop()
    required: boolean;

    /**
     * Shows the error text
     */
    @Prop()
    invalid: boolean;

    /**
     * Makes the input read only
     */
    @Prop()
    readonly: boolean;

    /**
     * Disables interaction with the input
     */
    @Prop()
    disabled: boolean = false;

    @State() selectedDate: string;
    @State() translations: any;
    @Element() el;

    private inputId = `datepicker-${Math.random().toString(36).substring(2, 11)}`;

    /**
     * Emitted when the date selection changes
     */
    @Event() atuiChange: EventEmitter<Date>;

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);

        if (!this.value) {
            this.value = new Date();
        }

        this.selectedDate = TimeDatePresentationUtil.getFormattedDate(
            this.value,
            'YYYY-MM-DD' as DateFormat,
        );
    }

    componentWillRender() {
        if (!this.selectedDate && this.value) {
            this.selectedDate = TimeDatePresentationUtil.getFormattedDate(
                this.value,
                'YYYY-MM-DD' as DateFormat,
            );
        }
    }

    render() {
        const getClassname = classlist(
            'placeholder-text-light pr -24 relative flex min-h-36 min-h-[36px] w-full flex-shrink flex-grow basis-0 gap-4 overflow-hidden rounded-md border border-solid overflow-ellipsis transition-colors duration-300 ease-in-out outline-none focus-within:ring-2 focus-within:outline-0',
            inputVariantsConfig,
        );

        const classname = getClassname({
            invalid: this.invalid,
            disabled: this.disabled,
            readonly: this.readonly,
        });

        return (
            <Host>
                <style>{`
                input[data-name="datepicker"]::-webkit-calendar-picker-indicator {
                    cursor: pointer;
                    position: absolute;
                    right: 8px;
                    top: 50%;
                    transform: translateY(-50%);
                }

                input[data-name="datepicker"] {
                    padding-right: 22px;
                    position: relative;
                }
                `}</style>
                <div class="flex flex-row">
                    <div class="flex-fill flex flex-col">
                        <div class="flex flex-col">
                            {(this.label ||
                                this.required ||
                                this.info_text) && (
                                <at-form-label
                                    label={this.label}
                                    for={this.inputId}
                                    required={this.required && !this.readonly}
                                    info_text={this.info_text}
                                />
                            )}

                            {this.hint_text && (
                                <span
                                    class="text-light mb-8 inline-block text-xs leading-tight"
                                    data-name="datepicker-hint"
                                >
                                    {this.hint_text}
                                </span>
                            )}

                            <div class={classname}>
                                <input
                                    type="date"
                                    id={this.inputId}
                                    name="datePicker"
                                    class="flex w-full cursor-pointer px-8 py-8 outline-0"
                                    readonly={this.readonly ? true : undefined}
                                    disabled={this.disabled ? true : undefined}
                                    required={this.required}
                                    value={this.selectedDate}
                                    min={TimeDatePresentationUtil.getFormattedDate(
                                        this.min_date,
                                        'YYYY-MM-DD' as DateFormat,
                                    )}
                                    max={TimeDatePresentationUtil.getFormattedDate(
                                        this.max_date,
                                        'YYYY-MM-DD' as DateFormat,
                                    )}
                                    data-name="datepicker"
                                    onChange={(event) => {
                                        const dateString = (
                                            event.target as HTMLInputElement
                                        ).value;

                                        const input =
                                            event.target as HTMLInputElement;

                                        // Validate year is 4 digits (between 0000-9999)
                                        const yearMatch =
                                            dateString.match(/^(\d{4})-/);
                                        if (
                                            !yearMatch ||
                                            yearMatch[1].length !== 4
                                        ) {
                                            // Reset to previous valid date if year is invalid
                                            (
                                                event.target as HTMLInputElement
                                            ).value = this.selectedDate;
                                            return;
                                        }

                                        this.selectedDate = dateString;
                                        this.invalid = !input.validity.valid;
                                        this.value = new Date(dateString);
                                        this.atuiChange.emit(this.value);
                                    }}
                                />
                            </div>
                        </div>

                        {this.error_text && this.invalid && (
                            <span
                                class="text-error text-sm"
                                data-name="datepicker-error"
                            >
                                {this.error_text}
                            </span>
                        )}
                    </div>
                </div>
            </Host>
        );
    }
}
